body {
  font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
  background-color: #000;
  color: #fff;
}

.wrapper {
  min-height: 100%;
  overflow: clip;
}
.page {
  position: absolute;
  width: 100%;
  min-height: 100vh;
  top: 0;
  left: 0;
  padding-top: 10vh;
  padding-bottom: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider {
  overflow: hidden;
  flex: 1 1 auto;
}
.slider__wrapper {
  display: flex;
}

.slider__slide {
  flex-shrink: 0;
}

.slide__body {
  width: 60vh;
  margin: 0 auto;
  max-width: 90vw;
  box-shadow: 0 0 1.857rem rgba(0, 0, 0, 0.5);
}
.slide__image {
  padding-bottom: 120%;
  position: relative;
  cursor: pointer;
}
.slide__picture {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}
.slider__scrollbar {
  position: absolute;
  width: 100%;
  height: 0.3125rem;
  bottom: 0;
  left: 0;
  opacity: 0.5;
  &:hover,
  &:active {
    opacity: 0.8;
    height: 0.9375rem;
  }
}

.slider__scrollbar .swiper-scrollbar-drag {
  opacity: 1;
  cursor: pointer;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #fff;
}
